___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Courier New', monospace;
  background-color: #1a1a2e;
  color: #ffffff;
  margin: 0;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

h1 {
  text-align: center;
  color: #ffffff;
  font-size: 3rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin-bottom: 30px;
  text-shadow:
    0 0 10px #4a4ae9,
    0 0 20px #4a4ae9,
    0 0 30px #4a4ae9;
}

#game-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  margin-top: 20px;
}

.gameboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #16213e;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 20px rgba(74, 74, 233, 0.5);
}

.board-label {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #4a4ae9;
}

.board {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 1px;
  width: 300px;
  height: 300px;
  border: 2px solid #4a4ae9;
  background-color: #0f3460;
}

.cell {
  background-color: #1a1a2e;
  cursor: pointer;
  transition: background-color 0.3s;
}

.cell:hover {
  background-color: #4a4ae9;
}

.ship {
  background-color: #4caf50;
}

.hit {
  background-color: #f44336;
}

.miss {
  background-color: #2196f3;
}

#message {
  margin-top: 20px;
  padding: 10px;
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center;
  background-color: #16213e;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(74, 74, 233, 0.5);
}

button {
  display: inline-block;
  margin: 10px;
  padding: 10px 20px;
  font-size: 1rem;
  font-family: 'Courier New', monospace;
  background-color: #e94560;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition:
    background-color 0.3s,
    transform 0.1s;
}

button:hover {
  background-color: #d63651;
  transform: translateY(-2px);
}

button:active {
  transform: translateY(0);
}

#rotate-ship {
  background-color: #4a4ae9;
}

#rotate-ship:hover {
  background-color: #3a3ad9;
}
